import React, { useEffect, useState } from 'react';
import NavBar from '../components/NavBar';
import Modal from '../components/Modal';
import Upload from "../components/Upload";
import GoogleMapReact from 'google-map-react';
import { useHistory } from "react-router-dom";
import search from "../assests/search.svg";
import Api from '../api/api';
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
import map from "../assests/map.svg";
import Logo from '../assests/logo_search.png';

const axios = require('axios');
const AnyReactComponent = ({ text }) => <div><img alt="Marker" src="https://res.cloudinary.com/di9ckb63k/image/upload/v1606463408/ref_1_bzw94s.png" />{text}</div>;

function LiveView(props) {
    const [arrLiveView, setarrLiveView] = useState([]);
    const [isError, setIsError] = useState();
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [arrSplittedValue, setArrSplittedValue] = useState([]);
    const [showData, setShowData] = useState(false);
    const [arrTagNumber, setArrTagNumber] = useState([]);
    const [selectedLiveViewHdl, setselectedLiveViewHdl] = useState("");
    const [isContentEditable, setisContentEditable] = useState(false);
    const [editedAttr, seteditedAttr] = useState({});
    const [showModal, setshowModal] = useState(false);
    let tempBody = {};
    let tempSelectedObj = {};
    let token = localStorage.getItem("b_token");
    let prefixUrl = localStorage.getItem("prefixUrl");
    let baseUrl = `https://${prefixUrl}.pim360.io/api`;
    let userName = localStorage.getItem("userName");
    let password = localStorage.getItem("password");
    let arrData = [];
    const history = useHistory();

    useEffect(() => {
        const queryParams = new URLSearchParams(window.location.search);
        const hdl = queryParams.get("TAGGED_ITEM");
        if (hdl) {
            doAuth(hdl);
        }
        if (window.location.pathname != '/') {
            const token = localStorage.getItem('b_token');
            const redirectPath = localStorage.getItem('redirectPath');
            if (token == null) {
                let arrLocation = window.location.href.split('/');
                localStorage.setItem('redirectPath', arrLocation[arrLocation.length - 1]);
                history.push('/');
            } else if (redirectPath != null) {
                history.push(redirectPath)
                localStorage.removeItem('redirectPath')
            }
        }
    }, []);


    const doAuth = (hdl) => {
        setIsLoading(true);
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=auth&username=${userName}&password=${password}&purl=${prefixUrl}`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") };
                fetchTaggedItems(hdl)
            }
        }).catch(error => {
            console.log("doAuth error-------->", JSON.stringify(error));
            setIsLoading(false);
        });
    };



    const fetchTaggedItems = (hdl) => {
        //doing api call for getting attributes data
        Api.objectByHdl('TAGGED_ITEM', hdl).then((response) => {
            if (response != null) {
                setShowData(true);
                //getting all keys from attrs
                let arrkeys = Object.keys(response.attrs);
                //initalize object
                let obj = {};
                //looping attribute keys and getting attribute values
                arrkeys.map((key) => {
                    obj[response.attrs[key].name] = response.attrs[key].value;
                });
                setData(response);
                setIsLoading(false);
            }
        }).catch(error => {
            console.log("fetchTaggedItems error---------->" + JSON.stringify(error));
            setIsLoading(false);
        });
    }

    const fetchSelectedLiveViewData = (selectedHdl) => {
        // setselectedLiveViewHdl(selectedLiveView);
        setIsLoading(true);
        let url = `${baseUrl}/customviews/${selectedHdl}`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(response => {
            if (response !== undefined && response !== null) {
                getResultUrl(response);
            }
        }).catch(error => {
            console.log("fetchSelectedLiveViewData error -------->" + error);
            setIsLoading(false);
        });
    }

    const firstApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=auth&username=${userName}&password=${password}&purl=${prefixUrl}`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => response.json())
            .then(data => {
                secondApi();
            })
            .catch((error) => {
                console.error('firstApi Error:', error);
                setIsLoading(false)
            });
    };

    const secondApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=import`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(tempBody),
        }).then(response => response.json())
            .then(data => {
                console.error('secondApi response ------------>', data);
                thirdApi();
            })
            .catch((error) => {
                console.error(' secondApi Error:', error);
                setIsLoading(false)
            });
    };

    const thirdApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=download-new`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => response.json())
            .then(data => {
                fourthApi(data.response);
            })
            .catch((error) => {
                console.error('thirdApi Error:', error);
                setIsLoading(false)
            });
    };

    const fourthApi = (hdl) => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=update&file_hdl=${hdl}`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(tempBody),
        }).then(response => response.json())
            .then(data => {
                console.error(' fourthApi response--------->:', data);
                setIsLoading(false);
                alert('Successfully updated.')
            })
            .catch((error) => {
                console.error(' fourthApi Error:', error);
                setIsLoading(false)
            });
    };


    const getResultUrl = (response) => {
        let createQueryBody = {
            "type": "TAGGED_ITEM",
            "eic": "",
            "filter": response.Data.conditions,
            "fields": response.Data.fields
        };
        let url = `${baseUrl}/queries`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(createQueryBody),
        }).then(response => response.json())
            .then(data => {
                return getFinalResult(data.resulturl).then((finalResult) => {
                    setIsLoading(false);
                    let arrFirstObjKeys = Object.keys(finalResult[0]).filter(e => typeof finalResult[0][e] == 'object')
                    let arrModifiedData = [];
                    let arrTagNumberData = [];
                    finalResult.map((item) => {
                        let obj = {};
                        arrFirstObjKeys.map((key) => {
                            if (item[key].name == "Tag Number" || item[key].name == "TAG NUMBER") {
                                arrTagNumberData.push(item[key].value);
                            }
                            obj[item[key].name] = item[key].value;
                        });
                        arrModifiedData.push(obj);
                    });
                    if (arrModifiedData.length > 0) {
                        if (arrModifiedData[0].hasOwnProperty("Tag Number") || arrModifiedData[0].hasOwnProperty("TAG NUMBER")) {
                            if (arrTagNumberData.length > 0) {
                                setArrTagNumber(arrTagNumberData);
                            }
                            setShowData(true);
                        }
                        else {
                            //finish process and show no data found
                            setShowData(true)
                        }
                    }
                });
            })
            .catch((error) => {
                console.error('Error:', error);
                setIsLoading(false)
            });
    };

    const getFinalResult = (queryResult, pageSize = 200, page = 0) => {
        let url = `${baseUrl}${queryResult}?limit=${pageSize}&skip=${page}`;
        axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") };
        return Api.getRequest(url).then((response) => {
            arrData.push(...response.data);
            if (response.hasmore) {
                return getFinalResult(queryResult, pageSize, (arrData.length / pageSize) * 200)
            } else {
                setIsLoading(false);
                return arrData;
            }
        });
    }

    const getAttributes = (selectedTag) => {
        let url = `${baseUrl}/objects/TAGGED_ITEM/id/${selectedTag}`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            setData(responseData);
        }).catch(error => {
            setIsLoading(false);
            console.log("getAttributes error -------->" + error);
        })
    }

    const onChangeAttributeValue = (value, key) => {
        let obj = {};
        if (Object.keys(editedAttr).length > 0) {
            obj = { ...editedAttr };
            tempSelectedObj = {};
        }
        else {
            obj = { ...tempSelectedObj };
        }
        obj[key] = value;
        seteditedAttr(obj);
    }

    const handleSubmitModal = (value) => {
        let finalObj = {
            finalObj: value,
            xlsxData: [editedAttr],
        };
        tempBody = finalObj;
        setshowModal(false);
        firstApi();
    }

    const showAttr = () => {
        let arrNameValue = [];
        let arrkeys = Object.keys(data.attrs)
        let obj = {};
        arrkeys.map((key) => {
            obj[data.attrs[key].name] = data.attrs[key].value;
            return '';
        })
        arrNameValue.push(obj);
        tempSelectedObj = arrNameValue[0];
        localStorage.setItem('pre_kml_data', JSON.stringify(arrNameValue))
        localStorage.setItem('inputsArray', JSON.stringify(arrSplittedValue))
        return <div className="flex mb-4">
            <div className="w-1/2 m-h-500px p-5 pr-0">
                {arrNameValue.length > 0 && arrNameValue[0].hasOwnProperty("Approved Longitude") && arrNameValue[0].hasOwnProperty("Approved Latitude") ?
                    <div className="w-full h-full shadow-lg">
                        <a target="_blank" href={`https://earth.google.com/web/search/Arsenal+fc/@${parseFloat(arrNameValue[0]["Approved Latitude"])},${parseFloat(arrNameValue[0]["Approved Longitude"])},9.09858867a,612.98636056d,35y,-40.1316669h,45.00000069t,0`}>Click here for Google Earth</a>
                        <GoogleMapReact
                            bootstrapURLKeys={{ key: "AIzaSyDNeF0TGdzD6PN696-ZWgdP7z9ltW0l1QY" }}
                            options={() => {
                                return {
                                    mapTypeControl: true,
                                    mapTypeId: "satellite"
                                }
                            }}
                            defaultCenter={{
                                lat: parseFloat(arrNameValue[0]["Approved Latitude"]),
                                lng: parseFloat(arrNameValue[0]["Approved Longitude"])
                            }}
                            center={{
                                lat: parseFloat(arrNameValue[0]["Approved Latitude"]),
                                lng: parseFloat(arrNameValue[0]["Approved Longitude"])
                            }}
                            distanceToMouse={() => { }}
                            defaultZoom={arrNameValue ? 18 : 6}
                        >
                            {arrNameValue.map((nameValue) => {
                                return <AnyReactComponent
                                    lat={nameValue["Approved Latitude"]}
                                    lng={nameValue["Approved Longitude"]}
                                />
                            })}
                        </GoogleMapReact>
                    </div>
                    : <div className="w-full h-full shadow-lg relative">
                        <div className="centerItem">
                            <img className="attr_chat mx-auto" draggable={false} src={map} />
                            <p className="text-center mt-3 text-purple-800">Invalid map data</p>
                        </div>
                    </div>
                }
            </div>
            <div className="w-1/2 m-h-500px p-5 pb-0">
                <div className="max-w rounded w-full shadow  p-5">
                    <p className="text-xl font-bold mb-3">Attributes</p>
                    <Accordion className="border-0" allowZeroExpanded preExpanded={["attributes"]}>
                        {arrNameValue.map((item, index) => {
                            return <AccordionItem className="shadow-md mb-3" key={item.Latitude} uuid="attributes" key={item + index}>
                                <AccordionItemHeading className="bg-gray-400 p-2">
                                    <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                        {/* <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="feather feather-arrow-right"><line x1="5" y1="12" x2="19" y2="12"></line><polyline points="12 5 19 12 12 19"></polyline></svg> */}
                                        &emsp;
                                        {arrNameValue[index].hasOwnProperty("Tag Number") ? arrNameValue[index]["Tag Number"] : arrNameValue[index]["TAG NUMBER"]}
                                    </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel className="liveView-item-container">
                                    <table className="table-auto w-full">
                                        <tbody>
                                            {Object.keys(item).map((key, index) => {
                                                return <tr key={key + index}>
                                                    <td className="border px-4 py-2">{key}</td>
                                                    {!isContentEditable
                                                        ?
                                                        <td className="border px-4 py-2">{item[key]}</td>
                                                        :
                                                        <td className="border px-4 py-2">
                                                            <input onChange={(e) => onChangeAttributeValue(e.target.value, key)} defaultValue={item[key]}></input>
                                                        </td>
                                                    }

                                                </tr>
                                            })}
                                        </tbody>
                                    </table>
                                </AccordionItemPanel>
                            </AccordionItem>
                        })}
                    </Accordion>
                </div>
            </div>
        </div>
    }


    const logout = () => {
        //remove all local storage
        localStorage.clear();
        history.push('/');
    }
    return (
        <div id="searchContainer">
            {/* <NavBar showLogoutButton={true} showSearch={false}></NavBar> */}
            <div class="header-nav">
                <div className="flex items-center flex-shrink-0 text-black mr-6 cursor-pointer" onClick={() => {
                    history.push('search')
                }}>
                    <img alt="Logo" className="w-1/2" src={Logo}></img>
                </div>
                <div>
                    <label className="block text-gray-700 text-sm font-bold mb-2">
                        &emsp;
                    </label>
                    <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none m-auto" type="button"
                        onClick={() => { logout() }}
                    >
                        Logout
                    </button>
                </div>

            </div>
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""
            }
            {showModal ? <Modal onClickCancel={() => { setshowModal(false) }} onClickSubmit={(value) => handleSubmitModal(value)} /> : ""}
            <div id="liveView-link-container">
                <div className="liveView-wrapper">
                    {showData ?
                        <div className="relative">
                            {data !== undefined && Object.keys(data).length > 0 &&
                                <div>
                                    {showAttr()}
                                </div>

                            }
                        </div>
                        :
                        <div className="centerItem">
                            <img draggable={false} width={300} src={search} alt='errorImage' />
                            <p className="text-center mt-3 text-purple-800">Unable to show the data here</p>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default LiveView;