import React, { useEffect, useState, useRef } from 'react';
import NavBar from '../components/NavBar';
import search from "../assests/search.svg";
import GoogleMapReact from 'google-map-react';
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
import mapboxgl from 'mapbox-gl';
import { useHistory } from "react-router-dom";
import Logo from '../assests/logo_search.png';

mapboxgl.accessToken =
    'pk.eyJ1IjoicmFuaml0aGRpbmFrYXJhbiIsImEiOiJja2h1bHd2azAwNmZvMnZvMm5jYXZubGt1In0.tG3PJN1RAiiFoDhCBFvADQ';

const AnyReactComponent = ({ text }) => <div><img alt="Marker" src="https://res.cloudinary.com/di9ckb63k/image/upload/v1606463408/ref_1_bzw94s.png" />{text}</div>;

function LiveViewAndTag(props) {
    const [arrLiveView, setarrLiveView] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [showData, setShowData] = useState(false);
    const [selectedLiveViewHdl, setselectedLiveViewHdl] = useState("");
    const [selectedLiveViewName, setselectedLiveViewName] = useState("");
    const [showMapBox, setShowMapBox] = useState(false);
    const [boxMapData, setBoxMapData] = useState({});
    const [googleMapData, setGoogleMapData] = useState([]);
    //map states
    const mapContainerRef = useRef(null);
    const [lng, setLng] = useState(5);
    const [lat, setLat] = useState(34);
    const [zoom, setZoom] = useState(5);
    let map = null;
    //local storage values
    let token = localStorage.getItem("b_token");
    let prefixUrl = localStorage.getItem("prefixUrl");
    let baseUrl = `https://${prefixUrl}.pim360.io/api`;
    let userName = localStorage.getItem("userName");
    let password = localStorage.getItem("password");

    //home page states
    const inputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline";
    const errInputClass = "shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline border-red-500";
    const [isError, setIsError] = useState();
    const [inputTag, setInputTag] = useState('');
    const [data, setData] = useState([]);
    const [arrSplittedValue, setArrSplittedValue] = useState([]);
    const [showcommonLogo, setShowcommonLogo] = useState(true);
    const [commonMsg, setCommonMsg] = useState('Select the liveview / tag number to show the data here');
    const history = useHistory();

    // Initialize map when component mounts
    useEffect(() => {
        if (window.location.pathname != '/') {
            const token = localStorage.getItem('b_token');
            const redirectPath = localStorage.getItem('redirectPath');
            if (token == null) {
                let arrLocation = window.location.href.split('/');
                localStorage.setItem('redirectPath', arrLocation[arrLocation.length - 1]);
                history.push('/');
            } else if (redirectPath != null) {
                history.push(redirectPath)
                localStorage.removeItem('redirectPath')
            }
        } else {
            fetchLiveView();
        }
    }, [])

    const show_mapBox = () => {
        map = new mapboxgl.Map({
            container: "map-container",
            style: 'mapbox://styles/mapbox/streets-v11',
            center: [lng, lat],
            zoom: zoom
        });
        // Add navigation control (the +/- zoom buttons)
        map.addControl(new mapboxgl.NavigationControl(), 'top-right');
        map.on('load', function () {
            // Add an image to use as a custom marker
            map.loadImage(
                'https://res.cloudinary.com/di9ckb63k/image/upload/v1606463408/ref_1_bzw94s.png',
                function (error, image) {
                    if (error) throw error;
                    map.addImage('custom-marker', image);
                    // console.log('boxMapData --------', JSON.stringify(boxMapData))
                    // Add a GeoJSON source with 2 points
                    map.addSource('points', {
                        'type': 'geojson',
                        'data': boxMapData,
                        cluster: true,
                        clusterMaxZoom: 14, // Max zoom to cluster points on
                        clusterRadius: 50 // Radius of each cluster when clustering points (defaults to 50)
                    });

                    // Add a symbol layer
                    map.addLayer({
                        'id': 'points',
                        'type': 'symbol',
                        'source': 'points',
                        'layout': {
                            'icon-image': 'custom-marker',
                            // get the title name from the source's "title" property
                            'text-field': ['get', 'title'],
                            'text-font': [
                                'Open Sans Semibold',
                                'Arial Unicode MS Bold'
                            ],
                            'text-offset': [0, 1.25],
                            'text-anchor': 'top'
                        }
                    });

                    map.addLayer({
                        id: 'clusters',
                        type: 'circle',
                        source: 'points',
                        filter: ['has', 'point_count'],
                        paint: {
                            // Use step expressions (https://docs.mapbox.com/mapbox-gl-js/style-spec/#expressions-step)
                            // with three steps to implement three types of circles:
                            //   * Blue, 20px circles when point count is less than 100
                            //   * Yellow, 30px circles when point count is between 100 and 750
                            //   * Pink, 40px circles when point count is greater than or equal to 750
                            'circle-color': [
                                'step',
                                ['get', 'point_count'],
                                '#51bbd6',
                                100,
                                '#f1f075',
                                750,
                                '#f28cb1'
                            ],
                            'circle-radius': [
                                'step',
                                ['get', 'point_count'],
                                20,
                                100,
                                30,
                                750,
                                40
                            ]
                        }
                    });

                    map.addLayer({
                        id: 'cluster-count',
                        type: 'symbol',
                        source: 'points',
                        filter: ['has', 'point_count'],
                        layout: {
                            'text-field': '{point_count_abbreviated}',
                            'text-font': ['DIN Offc Pro Medium', 'Arial Unicode MS Bold'],
                            'text-size': 12
                        }
                    });

                    map.addLayer({
                        id: 'unclustered-point',
                        type: 'circle',
                        source: 'points',
                        filter: ['!', ['has', 'point_count']],
                        paint: {
                            'circle-color': '#11b4da',
                            'circle-radius': 4,
                            'circle-stroke-width': 1,
                            'circle-stroke-color': '#fff'
                        }
                    });

                    // inspect a cluster on click
                    map.on('click', 'clusters', function (e) {
                        var features = map.queryRenderedFeatures(e.point, {
                            layers: ['clusters']
                        });
                        var clusterId = features[0].properties.cluster_id;
                        map.getSource('points').getClusterExpansionZoom(
                            clusterId,
                            function (err, zoom) {
                                if (err) return;

                                map.easeTo({
                                    center: features[0].geometry.coordinates,
                                    zoom: zoom
                                });
                            }
                        );
                    });

                    // When a click event occurs on a feature in
                    // the unclustered-point layer, open a popup at
                    // the location of the feature, with
                    // description HTML from its properties.
                    map.on('click', 'unclustered-point', function (e) {
                        var coordinates = e.features[0].geometry.coordinates.slice();
                        var mag = e.features[0].properties.mag;
                        var tsunami;

                        if (e.features[0].properties.tsunami === 1) {
                            tsunami = 'yes';
                        } else {
                            tsunami = 'no';
                        }

                        // Ensure that if the map is zoomed out such that
                        // multiple copies of the feature are visible, the
                        // popup appears over the copy being pointed to.
                        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                            coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
                        }

                        // new mapboxgl.Popup()
                        //   .setLngLat(coordinates)
                        //   .setHTML(
                        //     'magnitude: ' + mag + '<br>Was there a tsunami?: ' + tsunami
                        //   )
                        //   .addTo(map);
                    });

                    map.on('mouseenter', 'clusters', function () {
                        map.getCanvas().style.cursor = 'pointer';
                    });
                    map.on('mouseleave', 'clusters', function () {
                        map.getCanvas().style.cursor = '';
                    });

                    // When a click event occurs on a feature in the places layer, open a popup at the
                    // location of the feature, with description HTML from its properties.
                    map.on('click', 'points', function (e) {
                        var coordinates = e.features[0].geometry.coordinates.slice();
                        var description = e.features[0].properties.description;
                        // Ensure that if the map is zoomed out such that multiple
                        // copies of the feature are visible, the popup appears
                        // over the copy being pointed to.
                        while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                            coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
                        }

                        new mapboxgl.Popup()
                            .setLngLat(coordinates)
                            .setHTML(description)
                            .addTo(map);
                    });

                    // Change the cursor to a pointer when the mouse is over the places layer.
                    map.on('mouseenter', 'places', function () {
                        map.getCanvas().style.cursor = 'pointer';
                    });

                    // Change it back to a pointer when it leaves.
                    map.on('mouseleave', 'places', function () {
                        map.getCanvas().style.cursor = '';
                    });
                }
            );
        });
    };

    const fetchLiveView = () => {
        setIsLoading(true);
        let url = `${baseUrl}/customviews?type=LIVE_VIEW`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                setarrLiveView(responseData);
            }
            setIsLoading(false);
        }).catch(error => {
            console.log("error -------->" + error);
            setIsLoading(false);
        });
    };

    const doAuth = () => {
        setIsLoading(true);
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=auth&username=${userName}&password=${password}&purl=${prefixUrl}`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                fetchSelectedLiveViewData();
            }
        }).catch(error => {
            console.log("doAuth error-------->", JSON.stringify(error));
            setIsLoading(false);
        });
    };

    const formData = (response) => {
        let data = { ...response };
        let gMapData = [];
        data.features.map((item) => {
            //form mapBoxData
            let keys = Object.keys(item.properties);
            let tableData = "";
            tableData += "<p>";
            keys.map((k) => {
                if (k !== 'undefined' && k !== '(unresolved)') {
                    tableData += k;
                    tableData += " : ";
                    tableData += item.properties[k];
                    tableData += " <br> ";
                }
            });
            tableData += "</p>";
            let linkTitle = item.properties["TAG NUMBER"] ? item.properties["TAG NUMBER"] : item.properties["Tag Number"];
            let tagHdl = item.properties['undefined'] ? item.properties['undefined'] : item.properties['(unresolved)'];
            const link = `<a href='https://${prefixUrl}.pim360.io/object/TAGGED_ITEM/${tagHdl}' target='_blank'> ${linkTitle} <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-link"><path d="M10 13a5 5 0 0 0 7.54.54l3-3a5 5 0 0 0-7.07-7.07l-1.72 1.71"></path><path d="M14 11a5 5 0 0 0-7.54-.54l-3 3a5 5 0 0 0 7.07 7.07l1.71-1.71"></path></svg></a>`;
            let decriptionContent = `<div class='description-wrapper'><div class='header'></div><div class="content">${link}${tableData}</div></div>`

            //form gMapData
            gMapData.push({
                "lat": Number(item.geometry.coordinates[1]),
                "lng": Number(item.geometry.coordinates[0]),
                "ref_or_fix": "fix",
                "desc": decriptionContent
            });

            item.properties.description = decriptionContent;

            let lat = item.geometry.coordinates[1];
            let lng = item.geometry.coordinates[0];
            item.geometry.coordinates[0] = lng;
            item.geometry.coordinates[1] = lat;
        });

        setLat(data.features[0].geometry.coordinates[1]);
        setLng(data.features[0].geometry.coordinates[0]);
        setBoxMapData(data);
        setGoogleMapData(gMapData);
        setShowData(true);
        setIsLoading(false);
    }

    const formDataTagNumber = (response, dataToSet) => {
        let data = [...response];
        let gMapData = [];
        let keys = [];
        let latKey = '';
        let longKey = '';
        data.map((item) => {
            if (item.hasOwnProperty('Latitude')) {
                keys = ['Tag Number', 'Longitude', 'Latitude'];
                latKey = 'Latitude';
                longKey = 'Longitude';
            } else if (item.hasOwnProperty('LATITUDE')) {
                keys = ['TAG NUMBER', 'LONGITUDE', 'LATITUDE']
                latKey = 'LATITUDE';
                longKey = 'LONGITUDE';
            }
            else if (item.hasOwnProperty('Approved Latitude')) {
                keys = ['Tag Number', 'Approved Longitude', 'Approved Latitude'];
                latKey = 'Approved Latitude';
                longKey = 'Approved Longitude';
            }
            //form mapBoxData
            let tableData = "";
            tableData += "<p>";
            keys.map((k) => {
                if (k !== 'undefined' && k !== '(unresolved)') {
                    tableData += k;
                    tableData += " : ";
                    tableData += item[k];
                    tableData += " <br> ";
                }
            });
            tableData += "</p>";
            let linkTitle = item["TAG NUMBER"] ? item["TAG NUMBER"] : item["Tag Number"];
            let tagHdl = item['Handle'] ? item['Handle'] : '';
            const link = `<a href='https://${prefixUrl}.pim360.io/object/TAGGED_ITEM/${tagHdl}' target='_blank'> ${linkTitle} <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-link"><path d="M10 13a5 5 0 0 0 7.54.54l3-3a5 5 0 0 0-7.07-7.07l-1.72 1.71"></path><path d="M14 11a5 5 0 0 0-7.54-.54l-3 3a5 5 0 0 0 7.07 7.07l1.71-1.71"></path></svg></a>`;
            let decriptionContent = `<div class='description-wrapper'><div class='header'></div><div class="content">${link}${tableData}</div></div>`

            //form gMapData
            gMapData.push({
                "lat": Number(item[latKey]),
                "lng": Number(item[longKey]),
                "ref_or_fix": "fix",
                "desc": decriptionContent
            });

            item['description'] = decriptionContent;
        });

        setLat(data[0][latKey]);
        setLng(data[0][longKey]);
        setGoogleMapData(gMapData);
        setData(dataToSet);
        setIsLoading(false);
    }

    const fetchSelectedLiveViewData = () => {
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=liveview_to_geojson&live_view_name=${selectedLiveViewName}`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions)
            .then(response => response.json())
            .then(responseData => {
                if (responseData != null) {
                    formData(responseData);
                }
            })
            .catch(error => {
                setIsLoading(false);
                console.log('fetchSelectedLiveViewData error --------->', error)
                alert(error);
            });
    };

    useEffect(() => {
        if (showData) {
            if (showMapBox) {
                show_mapBox();
            }
            else {
                //initialize map
                window.InitializeMap(googleMapData);
            }
        }
        else if (data != undefined && data.length > 0) {
            //initialize map
            window.InitializeMap(googleMapData);
        }
    }, [showMapBox, showData, data]);

    const submitTagNumber = () => {
        setShowcommonLogo(false);
        //reset default value 
        setIsError(false);
        setShowData(false);

        //validation
        if (inputTag !== null && inputTag.length > 0) {
            setIsLoading(true)
            let arrData = [];
            let counter = 0;
            var splitedValue = inputTag.split(",").map((item) => {
                return item.trim();
            });
            setArrSplittedValue(splitedValue);
            splitedValue.map((id) => {
                let url = "https://" + localStorage.getItem("prefixUrl") + ".pim360.io/api/objects/TAGGED_ITEM/id/" + id;
                let token = localStorage.getItem("b_token");
                fetch(url, {
                    headers: {
                        Accept: "application/json",
                        Authorization: token,
                    }
                }).then(response => {
                    var responseValue = response.json();
                    return responseValue;
                }).then(responseData => {
                    if (responseData.attrs !== undefined && responseData.attrs !== null) {
                        arrData.push(responseData);
                    }
                    counter += 1;
                    if (counter === splitedValue.length) {
                        setIsLoading(false);
                        let arrNameValue = [];
                        arrData.map((item) => {
                            let arrkeys = Object.keys(item.attrs)
                            let obj = {};
                            arrkeys.map((key) => {
                                obj[item.attrs[key].name] = item.attrs[key].value;
                                return '';
                            })
                            arrNameValue.push(obj);
                            return '';
                        });
                        formDataTagNumber(arrNameValue, arrData)
                        return
                    }
                }).catch(error => {
                    console.log("error -------->" + error);
                    counter += 1
                    if (counter === splitedValue.length) {
                        setIsLoading(false);
                        setShowcommonLogo(true);
                        setCommonMsg('Something went wrong. Please try again')
                    }
                })
                return '';
            })
        }
        else {
            setIsError(true);
        }
    }

    const showAttr = () => {
        let arrNameValue = [];
        data.map((item) => {
            let arrkeys = Object.keys(item.attrs)
            let obj = {};
            arrkeys.map((key) => {
                obj[item.attrs[key].name] = item.attrs[key].value;
                return '';
            })
            arrNameValue.push(obj);
            return '';
        });
        console.log("arrNameValue ------->", JSON.stringify(arrNameValue))
        localStorage.setItem('pre_kml_data', JSON.stringify(arrNameValue))
        localStorage.setItem('inputsArray', JSON.stringify(arrSplittedValue))
        return <div className="flex my-4">
            <div className="w-1/2 m-h-500px p-5">
                {arrNameValue.length > 0 && (arrNameValue[0].hasOwnProperty("Latitude") || arrNameValue[0].hasOwnProperty("LATITUDE") || arrNameValue[0].hasOwnProperty("Approved Latitude")) ?
                    <div className="w-full h-full shadow-lg">
                        {/* <GoogleMapReact
                            bootstrapURLKeys={{ key: "AIzaSyDNeF0TGdzD6PN696-ZWgdP7z9ltW0l1QY" }}
                            options={() => {
                                return {
                                    mapTypeControl: true
                                }
                            }}
                            defaultCenter={{
                                lat: parseFloat(arrNameValue[0].hasOwnProperty("Latitude") ? arrNameValue[0]["Latitude"] : (arrNameValue[0].hasOwnProperty("LATITUDE") ? arrNameValue[0]["LATITUDE"] : (arrNameValue[0].hasOwnProperty("Approved Latitude") ? arrNameValue[0]["Approved Latitude"] : 0))),
                                lng: parseFloat(arrNameValue[0].hasOwnProperty("Longitude") ? arrNameValue[0]["Longitude"] : (arrNameValue[0].hasOwnProperty("LONGITUDE") ? arrNameValue[0]["LONGITUDE"] : (arrNameValue[0].hasOwnProperty("Approved Longitude") ? arrNameValue[0]["Approved Longitude"] : 0))),
                            }}
                            center={{
                                lat: parseFloat(arrNameValue[0].hasOwnProperty("Latitude") ? arrNameValue[0]["Latitude"] : (arrNameValue[0].hasOwnProperty("LATITUDE") ? arrNameValue[0]["LATITUDE"] : (arrNameValue[0].hasOwnProperty("Approved Latitude") ? arrNameValue[0]["Approved Latitude"] : 0))),
                                lng: parseFloat(arrNameValue[0].hasOwnProperty("Longitude") ? arrNameValue[0]["Longitude"] : (arrNameValue[0].hasOwnProperty("LONGITUDE") ? arrNameValue[0]["LONGITUDE"] : (arrNameValue[0].hasOwnProperty("Approved Longitude") ? arrNameValue[0]["Approved Longitude"] : 0))),
                            }}
                            distanceToMouse={() => { }}
                            defaultZoom={arrNameValue ? 18 : 6}
                        >
                            {arrNameValue.map((nameValue) => {
                                return <AnyReactComponent
                                    lat={parseFloat(nameValue.hasOwnProperty("Latitude") ? nameValue["Latitude"] : (nameValue.hasOwnProperty("LATITUDE") ? nameValue["LATITUDE"] : (nameValue.hasOwnProperty("Approved Latitude") ? nameValue["Approved Latitude"] : 0)))}
                                    lng={parseFloat(nameValue.hasOwnProperty("Longitude") ? nameValue["Longitude"] : (nameValue.hasOwnProperty("LONGITUDE") ? nameValue["LONGITUDE"] : (nameValue.hasOwnProperty("Approved Longitude") ? nameValue["Approved Longitude"] : 0)))}
                                />
                            })}
                        </GoogleMapReact> */}
                        <div id="dvMap" className={showMapBox ? "opacity-25" : "opacity-100 min-h-full width m-4 map-height"}></div>
                    </div>
                    : <div className="w-full h-full shadow-lg relative">
                        <div className="centerItem">
                            <img className="attr_chat mx-auto" draggable={false} src={map} />
                            <p className="text-center mt-3 text-purple-800">Invalid map data</p>
                        </div>
                    </div>
                }
            </div>
            <div className="w-1/2 m-h-500px">
                <div className="max-w rounded w-full shadow  p-5">
                    <p className="text-xl font-bold mb-3">Attributes</p>
                    <Accordion className="border-0" allowZeroExpanded preExpanded={["attributes"]}>
                        {arrNameValue.map((item, index) => {
                            return <AccordionItem className="shadow-md mb-3" key={index} uuid="attributes">
                                <AccordionItemHeading className="bg-gray-400 p-2">
                                    <AccordionItemButton className="w-full border-none outline-none focus:border-0 inline-flex">
                                        &emsp;
                                        {arrSplittedValue[index]}
                                    </AccordionItemButton>
                                </AccordionItemHeading>
                                <AccordionItemPanel>
                                    <table class="table-auto w-full">
                                        <tbody>
                                            {Object.keys(item).map((key) => {
                                                return <tr>
                                                    <td class="border px-4 py-2">{key}</td>
                                                    <td class="border px-4 py-2">{item[key]}</td>
                                                </tr>
                                            })}
                                        </tbody>
                                    </table>
                                </AccordionItemPanel>
                            </AccordionItem>
                        })}
                    </Accordion>
                </div>
            </div>
        </div>
    }

    const handleLiveViewSubmit = () => {
        setShowcommonLogo(false);
        setData([]);
        if (selectedLiveViewName != '') {
            doAuth();
        }
    }

    const logout = () => {
        //remove all local storage
        localStorage.clear();
        history.push('/');
    }

    return (
        <div id="searchContainer">
            {/* <NavBar showLogoutButton={true} showSearch={false}></NavBar> */}
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""
            }
            <div class="header-nav">
                <div className="flex items-center flex-shrink-0 text-black mr-6 cursor-pointer" onClick={() => window.location.reload()}>
                    <img alt="Logo" className="w-1/2" src={Logo}></img>
                </div>
                <div>
                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                        LiveView
                    </label>
                    <div className="inline-block relative w-1/1">
                        <select
                            class="block appearance-none w-full bg-gray-300 border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
                            onChange={(e) => {
                                var textVal = e.target.options[e.target.selectedIndex].text;
                                // doAuth(textVal, e.target.value);
                                setselectedLiveViewName(textVal);
                                setselectedLiveViewHdl(e.target.value);
                            }}>
                            <option value="">Select</option>
                            {arrLiveView != null && arrLiveView.length > 0 ? arrLiveView.map((liveView, index) => {
                                return <option name={liveView.Name} value={liveView.Hdl}>{liveView.Name}</option>
                            }) : ""
                            }
                        </select>
                        <div class="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                            <svg class="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
                        </div>
                    </div>
                    <div className="inline-block ml-5">
                        <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none m-auto" type="button"
                            onClick={() => { handleLiveViewSubmit() }}
                        >
                            Submit
                        </button>
                    </div>
                </div>
                <div>
                    <div className="inline-block">
                        <label className="block text-gray-700 text-sm font-bold mb-2">
                            Tag Number
                        </label>
                        <input className={isError ? errInputClass : inputClass} value={inputTag}
                            id="tagNumber" type="text" placeholder="Enter your tag number" onChange={(e) => setInputTag(e.target.value)}
                        />
                    </div>
                    <div className="inline-block ml-5">
                        <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none m-auto" type="button"
                            onClick={() => { submitTagNumber() }}
                        >
                            Submit
                        </button>
                    </div>
                    <div className="inline-block ml-5">
                        <label className="block text-gray-700 text-sm font-bold mb-2">
                            &emsp;
                        </label>
                        <button className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded focus:outline-none m-auto" type="button"
                            onClick={() => { logout() }}
                        >
                            Logout
                        </button>
                    </div>
                    {/* {isError ? <p className="text-red-500 pl-2 text-xs pt-1">Tag name is required </p> : ""} */}
                </div>

            </div>

            <div id="liveView-container">
                <div className="w-full">
                    {showData &&
                        <div className="relative">
                            <div className="m-4">
                                <label className="inline-block text-gray-700 text-sm font-bold " htmlFor="username">
                                    Selected LiveView:
                                </label>
                                <p className="inline-block ml-4">{selectedLiveViewName}</p>
                            </div>
                            <div className="m-4">
                                <button className={showMapBox ? "btn" : "btn-selected"} onClick={() => setShowMapBox(false)}>Google Map</button>
                                <button className={showMapBox ? "btn-selected" : "btn"} onClick={() => { setShowMapBox(true) }}>Mapbox</button>
                            </div>
                            {showMapBox &&
                                <div id="map-container" className='map-container m-4 map-height width' ref={mapContainerRef} />
                            }
                            {!showMapBox ? <div id="dvMap" className={showMapBox ? "opacity-25" : "opacity-100 min-h-full width m-4 map-height"}></div> : ""}
                        </div>
                    }
                    {data !== undefined && data.length > 0 &&
                        <div>
                            {showAttr()}
                        </div>
                    }
                    {showcommonLogo && (
                        <div className="centerItem">
                            <img draggable={false} alt='' width={300} src={search} />
                            <p className="text-center mt-3 text-purple-800">{commonMsg}</p>
                        </div>
                    )
                    }
                </div>
            </div>
        </div>
    );
}

export default LiveViewAndTag;