import React from 'react';
import LoginScreen from './screens/LoginScreen';
import HomeScreen from './screens/HomeScreen';
import LiveView from './screens/LiveView';
import LiveToMap from './screens/LiveToMap';
import LiveToShape from './screens/LiveToShape';
import LiveToShape_v2 from './screens/LiveToShape_v2';
import LiveViewWithTable from "./screens/LiveViewWithTable";
import LiveViewToLink from "./screens/LiveViewToLink";
import esriMapPOC from './screens/ESRIMapPOC';
import LinearAsset from "./screens/LinearAsset";
import LiveViewAndTag from "./screens/LiveViewAndTag";
import Poc from './screens/Poc';
import "./App.css";
import { BrowserRouter as Router, Route } from "react-router-dom";


function App() {
  return (
    <div>
      <Router>
        <Route exact path="/" component={LoginScreen} />
        <Route exact path="/home" component={HomeScreen} />
        <Route exact path="/liveView" component={LiveView} />
        <Route exact path="/poc" component={Poc} />
        <Route exact path="/linearAsset" component={LinearAsset} />
        <Route exact path="/liveview2map" component={LiveToMap} />
        <Route exact path="/l2sv1" component={LiveToShape} />
        <Route exact path="/l2sv2" component={LiveToShape_v2} />
        <Route exact path="/esriMapPOC" component={esriMapPOC} />
        <Route exact path="/liveView_Table" component={LiveViewWithTable} />
        <Route exact path="/liveview2link" component={LiveViewToLink} />
        <Route exact path="/search" component={LiveViewAndTag} />
      </Router>
    </div>
  );
}

export default App;
