import React, { useEffect, useState } from 'react';
import NavBar from '../components/NavBar';
import Modal from '../components/Modal';
import GoogleMapReact from 'google-map-react';
import search from "../assests/search.svg";
import Api from '../api/api';
import {
    Accordion,
    AccordionItem,
    AccordionItemHeading,
    AccordionItemButton,
    AccordionItemPanel,
} from 'react-accessible-accordion';
import map from "../assests/map.svg";
const axios = require('axios');
const AnyReactComponent = ({ text }) => <div><img alt="Marker" src="https://img.icons8.com/fluent/48/000000/marker-storm.png" />{text}</div>;

function LiveViewWithTable(props) {
    const [arrLiveView, setarrLiveView] = useState([]);
    const [isError, setIsError] = useState();
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);
    const [arrSplittedValue, setArrSplittedValue] = useState([]);
    const [showData, setShowData] = useState(false);
    const [arrTagNumber, setArrTagNumber] = useState([]);
    const [selectedLiveViewHdl, setselectedLiveViewHdl] = useState("");
    const [showModal, setshowModal] = useState(false);
    const [arrFormedTableData, setArrFormedTableData] = useState([]);
    const [editedArrFormedTabledData, setEditedArrFormedTabledData] = useState([]);

    let tempBody = {};
    let tempArrFormedTableData = [];
    let token = localStorage.getItem("b_token");
    let prefixUrl = localStorage.getItem("prefixUrl");
    let baseUrl = `https://${prefixUrl}.pim360.io/api`;
    let userName = localStorage.getItem("userName");
    let password = localStorage.getItem("password");
    let arrData = [];

    useEffect(() => {
        fetchLiveView();
    }, []);

    useEffect(() => {
        if (arrTagNumber.length > 0) {
            // arrTagNumber.map((tagItem, index) => {
            //     getAttributes(tagItem, arrTagNumber.length === (index + 1));
            // });
            getAttributes();
        }
    }, [arrTagNumber]);

    const fetchLiveView = () => {
        setIsLoading(true);
        let url = `${baseUrl}/customviews?type=LIVE_VIEW`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(responseData => {
            if (responseData !== undefined && responseData !== null) {
                setarrLiveView(responseData);
            }
            setIsLoading(false);
        }).catch(error => {
            console.log("error -------->" + error);
            setIsLoading(false);
        });
    };

    const fetchSelectedLiveViewData = (e, selectedLiveView, selectedHdl) => {
        setselectedLiveViewHdl(selectedLiveView);
        setIsLoading(true);
        let url = `${baseUrl}/customviews/${selectedHdl}`;
        fetch(url, {
            headers: {
                Accept: "application/json",
                Authorization: token,
            }
        }).then(response => {
            var responseValue = response.json();
            return responseValue;
        }).then(response => {
            if (response !== undefined && response !== null) {
                getResultUrl(response);
            }
        }).catch(error => {
            console.log("fetchSelectedLiveViewData error -------->" + error);
            setIsLoading(false);
        });
    }

    const firstApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=auth&username=${userName}&password=${password}&purl=${prefixUrl}`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => response.json())
            .then(data => {
                console.log('firstApi res ----->', JSON.stringify(data));
                secondApi();
            })
            .catch((error) => {
                console.error('firstApi Error:', error);
                setIsLoading(false)
            });
    };

    const secondApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=import`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(tempBody),
        }).then(response => response.json())
            .then(data => {
                console.error('secondApi response ------------>', data);
                thirdApi();
            })
            .catch((error) => {
                console.error(' secondApi Error:', error);
                setIsLoading(false)
            });
    };

    const thirdApi = () => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=download-new`;
        var myHeaders = new Headers();
        var requestOptions = {
            method: 'GET',
            headers: myHeaders,
            redirect: 'follow'
        };
        fetch(url, requestOptions).then(response => response.json())
            .then(data => {
                console.log('thirdApi res ----->', JSON.stringify(data));
                fourthApi(data.response);
            })
            .catch((error) => {
                console.error('thirdApi Error:', error);
                setIsLoading(false)
            });
    };

    const fourthApi = (hdl) => {
        setIsLoading(true)
        let url = `https://pim4.azurewebsites.net/api/pim360?function_name=update&file_hdl=${hdl}`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(tempBody),
        }).then(response => response.json())
            .then(data => {
                console.error(' fourthApi response--------->:', data);
                setIsLoading(false);
                alert('Successfully updated.')
            })
            .catch((error) => {
                console.error(' fourthApi Error:', error);
                setIsLoading(false)
            });
    };


    const getResultUrl = (response) => {
        let createQueryBody = {
            "type": "TAGGED_ITEM",
            "eic": "",
            "filter": response.Data.conditions,
            "fields": response.Data.fields
        };
        let url = `${baseUrl}/queries`;
        fetch(url, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": token
            },
            body: JSON.stringify(createQueryBody),
        }).then(response => response.json())
            .then(data => {
                return getFinalResult(data.resulturl).then((finalResult) => {
                    // console.log('finalResult ------->', JSON.stringify(finalResult));
                    setIsLoading(false);
                    let arrFirstObjKeys = Object.keys(finalResult[0]).filter(e => typeof finalResult[0][e] == 'object')
                    let arrModifiedData = [];
                    let arrTagNumberData = [];
                    finalResult.map((item) => {
                        let obj = {};
                        arrFirstObjKeys.map((key) => {
                            if (item[key].name == "Tag Number" || item[key].name == "TAG NUMBER") {
                                arrTagNumberData.push(item[key].value);
                            }
                            obj[item[key].name] = item[key].value;
                        });
                        arrModifiedData.push(obj);
                    });
                    if (arrModifiedData.length > 0) {
                        if (arrModifiedData[0].hasOwnProperty("Tag Number") || arrModifiedData[0].hasOwnProperty("TAG NUMBER")) {
                            if (arrTagNumberData.length > 0) {
                                setArrTagNumber(arrTagNumberData);
                            }
                            setShowData(true);
                        }
                        else {
                            //finish process and show no data found
                            setShowData(true)
                        }
                    }
                });
            })
            .catch((error) => {
                console.error('Error:', error);
                setIsLoading(false)
            });
    };

    const getFinalResult = (queryResult, pageSize = 200, page = 0) => {
        let url = `${baseUrl}${queryResult}?limit=${pageSize}&skip=${page}`;
        axios.defaults.headers.common = { 'Authorization': localStorage.getItem("b_token") };
        return Api.getRequest(url).then((response) => {
            arrData.push(...response.data);
            if (response.hasmore) {
                return getFinalResult(queryResult, pageSize, (arrData.length / pageSize) * 200)
            } else {
                setIsLoading(false);
                return arrData;
            }
        });
    }

    const getAttributes = () => {
        setIsLoading(true);
        let tempData = [];
        arrTagNumber.map(async (item, index) => {
            let url = `${baseUrl}/objects/TAGGED_ITEM/id/${item}`;
            await fetch(url, {
                headers: {
                    Accept: "application/json",
                    Authorization: token,
                }
            }).then(response => {
                var responseValue = response.json();
                return responseValue;
            }).then(responseData => {
                tempData.push(responseData);
                if (arrTagNumber.length === (index + 1)) {
                    setData(tempData);
                    setIsLoading(false);
                }
            }).catch(error => {
                setIsLoading(false);
                console.log("getAttributes error -------->" + error);
            })
        });

    }

    const onChangeAttributeValue = (value, key, itemIndex) => {
        let tempArr = [];
        if (editedArrFormedTabledData.length === 0) {
            setArrFormedTableData(tempArrFormedTableData);
            tempArr = [...tempArrFormedTableData];
        }
        else {
            tempArr = [...editedArrFormedTabledData];
        }
        let objToEdit = tempArr[itemIndex];
        // console.log("obj to edit", JSON.stringify(objToEdit))
        objToEdit[key] = value;
        objToEdit["isEdited"] = true;
        // console.log('tempArr ------->', JSON.stringify(tempArr));
        setEditedArrFormedTabledData(tempArr);
    };

    const handleSubmitModal = (value) => {
        let editedRows = editedArrFormedTabledData.filter(item => item.isEdited === true);
        // console.log(editedRows.length)
        // console.log("editesRows ------->", JSON.stringify(editedRows));
        let finalObj = {
            finalObj: value,
            xlsxData: editedRows,
        };
        tempBody = finalObj;
        setshowModal(false);
        firstApi();
    }

    const showAttr = () => {
        // console.log("show attr")
        let arrFormedTableData = [];
        data.map((item) => {
            let arrkeys = Object.keys(item.attrs)
            let obj = {};
            arrkeys.map((key) => {
                obj[item.attrs[key].name] = item.attrs[key].value;
                return '';
            });
            obj["isEdited"] = false;
            arrFormedTableData.push(obj);
        });
        //set formed data in arr
        tempArrFormedTableData = arrFormedTableData;

        let tableHeaderKeys = ["TAG NUMBER", "ASSET", "CLASS NAME", "DESCRIPTION"]
        localStorage.setItem('pre_kml_data', JSON.stringify(arrFormedTableData))
        localStorage.setItem('inputsArray', JSON.stringify(arrSplittedValue))
        return <div className="mb-4 max-w rounded w-full shadow  p-5">
            <div className="flex mb-2">
                <p className="text-xl font-bold mb-3">Attributes</p>
                <div className="ml-5">
                    {/* <button className="bg-white py-2 px-3 border border-gray-300 rounded-md shadow-sm text-sm leading-4 font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 mr-3" onClick={() => { }}>
                        Reset
                    </button> */}
                    <button className="inline-flex justify-center py-2 px-3 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-purple-900 hover:bg-purple-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500" onClick={() => setshowModal(true)}>
                        Save
                    </button>
                </div>
            </div>
            <div className="table-wrapper">
                <table className="table-auto w-full withTable">
                    <thead>
                        <tr key="head_row">
                            {tableHeaderKeys.map((headerKey) => {
                                return <td>{headerKey}</td>
                            })}
                        </tr>
                    </thead>
                    <tbody>
                        {arrFormedTableData.map((item, index) => {
                            return <tr key={'body_row' + index}>
                                {tableHeaderKeys.map((headerKey, childIndex) => {
                                    return <td key={"body_td_" + headerKey + index + childIndex}>
                                        <input onChange={(e) => onChangeAttributeValue(e.target.value, headerKey, index)} defaultValue={item[headerKey]}></input>
                                    </td>
                                })}
                            </tr>
                        })}
                    </tbody>
                </table>
            </div>
        </div>
    }
    return (
        <div>
            <NavBar showLogoutButton={true} showSearch={false}></NavBar>
            {isLoading ?
                <div className="popUpContainer">
                    <div className="loader"></div>
                </div>
                : ""
            }
            {showModal ? <Modal onClickCancel={() => { setshowModal(false) }} onClickSubmit={(value) => handleSubmitModal(value)} /> : ""}
            <div id="liveView-container">
                <div className="sideBar bg-purple-900">
                    {arrLiveView != null && arrLiveView.length > 0 ? arrLiveView.map((liveView, index) => {
                        return (<div key={liveView.name + liveView.Hdl + index}>
                            <button
                                onClick={(e) => fetchSelectedLiveViewData(e, liveView.Name, liveView.Hdl)}

                                className="bg-gray-300 hover:bg-gray-400 text-gray-800 font-bold py-2 px-4 rounded inline-flex my-2 justify-center w-full">
                                {liveView.Name}
                            </button>
                        </div>)
                    }) : ""}
                </div>
                <div className="liveView-wrapper">
                    {showData ?
                        <div className="relative">
                            <div className="m-4">
                                <label className="inline-block text-gray-700 text-sm font-bold " htmlFor="username">
                                    Selected LiveView:
                                </label>
                                <p className="inline-block ml-4">{selectedLiveViewHdl}</p>
                            </div>
                            {/* {arrTagNumber.length > 0 &&
                                <div className="m-4 mt-0">
                                    <label className="block text-gray-700 text-sm font-bold mb-2" htmlFor="username">
                                        Tag Number
                                    </label>
                                    <div className="inline-block relative w-1/4">
                                        <select
                                            className="block appearance-none w-full bg-gray-300 border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline"
                                            onChange={(e) => {
                                                getAttributes(e.target.value)
                                            }}>
                                            <option value="">Select</option>
                                            {
                                                arrTagNumber.map((tag, index) => {
                                                    return <option value={tag} key={tag + index}>{tag}</option>
                                                })
                                            }
                                        </select>
                                        <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center px-2 text-gray-700">
                                            <svg className="fill-current h-4 w-4" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20"><path d="M9.293 12.95l.707.707L15.657 8l-1.414-1.414L10 10.828 5.757 6.586 4.343 8z" /></svg>
                                        </div>
                                    </div>
                                    <div className="inline-block relative w-1/4 ml-3">
                                        {
                                            data !== undefined && Object.keys(data).length > 0 ?
                                                <a target="_blank" href={`https://${prefixUrl}.pim360.io/object/TAGGED_ITEM/${data.hdl}`}>
                                                    <img width="15%" src={`https://${prefixUrl}.acl360.io/img/pim360-logo.svg`} onError={(e) => {
                                                        
                                                        e.target.src = `https://${prefixUrl}.acl360.io/img/pim360_logo.png`
                                                    }} />
                                                </a>
                                                : ''
                                        }
                                    </div>
                                </div>

                            }
                            */}
                            {data !== undefined && data.length > 0 &&
                                <div>
                                    {showAttr()}
                                </div>

                            }
                        </div>
                        :
                        <div className="centerItem">
                            <img draggable={false} width={300} src={search} alt="" />
                            <p className="text-center mt-3 text-purple-800">Select the liveview to show the data here</p>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default LiveViewWithTable;